import { Component, OnInit } from "@angular/core";

import { TemplateService } from "../services/template.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Template } from "app/shared/models/template.model";

@Component({
  selector: "app-edit-template",
  templateUrl: "./edit-template.component.html",
  styleUrls: ["./edit-template.component.scss"]
})
export class EdittemplateComponent implements OnInit {
  updatedtemplate;
  templateid;

  constructor(
    public templateservice: TemplateService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedtemplate = {};

    this.templateid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.templateservice.gettemplateById(this.templateid).subscribe(
      (data: any) => {
        console.log("templatelist>>", data);
        this.updatedtemplate = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedtemplate);

    swal
      .fire({
        title: "Do you want to edit the template details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.templateservice.edittemplate(this.updatedtemplate).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Template User has been edited.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
