import { Component, OnInit } from "@angular/core";
import { TemplateService } from "../services/template.service";
import { Router, ActivatedRoute } from "@angular/router";
import { Template } from "../../../shared/models/template.model";

import swal from "sweetalert2";

@Component({
  selector: "app-add-template",
  templateUrl: "./add-template.component.html",
  styleUrls: ["./add-template.component.scss"]
})
export class AddTemplateComponent implements OnInit {
  newtemplate;
  submitting: boolean;
  constructor(
    public templateservice: TemplateService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newtemplate = {};
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newtemplate);

    swal
      .fire({
        title: "Do you want to add the Template ?",
        text: "New user will be added in Template List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.templateservice.addTemplate(this.newtemplate).subscribe(
            (data: any) => {
              swal.fire("Add!", "Template has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
