import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { BaseService } from "app/shared/services/base.service";
import { Template } from "app/shared/models/template.model";

@Injectable()
export class TemplateService extends BaseService {
  testurl;

  constructor(public http: HttpClient) {
    super("template");
  }

  addTemplate(data) {
    console.log("url>>", this.url);
    console.log("data>>", data);

    return this.http.post(this.url, { data: data }, this.getOptions());
  }

  getTemplate() {
    return this.http.get(this.url, this.getOptions());
  }

  deleteTemplate(id) {
    return this.http.delete(this.url + id);
  }
  gettemplateById(id) {
    return this.http.get(this.url + id, this.getOptions());
  }
  edittemplate(data) {
    return this.http.put(
      this.url + data._id,
      { data: data },
      this.getOptions()
    );
  }
}
