import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { TemplateService } from "./services/template.service";
import swal from "sweetalert2";
import { Template } from "../../shared/models/template.model";

@Component({
  selector: "app-template",
  templateUrl: "./template.component.html",
  styleUrls: ["./template.component.scss"]
})
export class TemplateComponent implements OnInit {
  templateList: Array<Template>;

  constructor(public templateservice: TemplateService, private router: Router) {
    this.templateList = [];
  }

  ngOnInit() {
    this.templateservice.getTemplate().subscribe(
      (data: any) => {
        console.log("templatelist>>", data);
        this.templateList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("template id>>", id);
    this.router.navigate(["/sms/template/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the user ?",
        text: "You won't be able to revert the deleted user",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.templateservice.deleteTemplate(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "User has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
